




































































































import Vue from "vue";
import moment from "moment";
import MainLayout from "../components/MainLayout.vue";
import { mapActions } from "vuex";
import { ChartSeries } from "@/types/ChartSeries";
import ReviewFilters from "@/components/ReviewFilters.vue";
import { ReviewFiltersType } from "@/types/ReviewFiltersType";

export default Vue.extend({
  name: "PerformanceReport",
  components: {
    MainLayout,
    ReviewFilters,
  },
  data: () => ({
    averageAccuracyData: [] as ChartSeries[],
    totalAnalyzedFaultyData: [] as ChartSeries[],
    successfulFaultyData: [] as ChartSeries[],
    hasData: true,
    textData: {
      startDate: "",
      endDate: "",
      averageAccuracy: 0,
      faultyReviews: 0,
      successfulReviews: 0,
      analyzedReviews: 0,
      totalReviews: 0,
    },
  }),
  methods: {
    ...mapActions(["getReviewHistogram"]),
    async getHistogramDetails(filters: ReviewFiltersType) {
      const res = await this.getReviewHistogram({
        repoUrl: this.$route.params.repoUrl,
        filters,
      });
      if (res) {
        this.textData = {
          startDate: moment(filters.startDate).format("DD MMM YYYY"),
          endDate: moment(filters.endDate).format("DD MMM YYYY"),
          analyzedReviews: res.totalFaulty + res.totalSuccessful,
          averageAccuracy: Number(res.averageAccuracy.toFixed(1)),
          faultyReviews: res.totalFaulty,
          successfulReviews: res.totalSuccessful,
          totalReviews: res.totalReviews,
        };
        this.hasData = true;
        this.averageAccuracyData = [{ name: "Average Review Accuracy", data: res.accuracyDataSet }];
        this.totalAnalyzedFaultyData = [
          { name: "total", data: res.totalDataSet },
          { name: "analyzed", data: res.analyzedDataSet },
          { name: "faulty", data: res.faultyDataSet },
        ];
        this.successfulFaultyData = [res.totalFaulty, res.totalSuccessful];
      } else {
        this.hasData = false;
      }
    },
  },
  computed: {
    lineOptions: () => {
      return {
        theme: {
          mode: "dark",
          palette: "palette2",
        },
        chart: {
          type: "line",
          background: "#1E1E1E",
        },
        xaxis: {
          type: "datetime",
          labels: {
            datetimeUTC: false,
          },
        },
        markers: {
          size: 5,
        },
        yaxis: {
          min: 0,
          max: 100,
          tickAmount: 5,
        },
        title: {
          text: "Average Review Accuracy by Date",
        },
      };
    },
    splineAreaOptions: () => {
      return {
        theme: {
          mode: "dark",
          palette: "palette2",
        },
        chart: {
          type: "area",
          background: "#1E1E1E",
        },
        stroke: {
          curve: "smooth",
        },
        xaxis: {
          type: "datetime",
          labels: {
            datetimeUTC: false,
          },
        },
        title: {
          text: "Total/Analyzed/Faulty Review Count by Date",
        },
      };
    },
    pieOptions: () => {
      return {
        theme: {
          mode: "dark",
          palette: "palette2",
        },
        chart: {
          type: "pie",
          background: "#1E1E1E",
        },
        labels: ["Faulty", "Successful"],

        title: {
          text: "Successful - Faulty Reviews Ratio",
        },
      };
    },
    totalReviewsTooltipText: () => `Total reviews is the total number of reviews that were\n
                                  performed by the reviewer in the specified time period`,
    analyzedReviewsTooltipText: () => `Analyzed review count is the number of reviews performed\n
                                     by the reviewer in the specified time period that have been\n
                                     analyzed to check what percentage of injected bugs they\n
                                     were able to catch during their review.`,
    faultyReviewsTooltipText: () => `Faulty review count is the number of analyzed reviews of\n
                                   the reviewer in the specified time period that caught a\n
                                   percentage of bugs that was below the minimum threshold\n
                                   set by the admin. E.g. if the threshold is 70% and a review\n
                                   caught 69% of bugs, it is considered faulty.`,
    successfulReviewsTooltipText: () => `Successful review count is the number of analyzed reviews of\n
                                   the reviewer in the specified time period that caught a\n
                                   percentage of bugs that was above or equal to the minimum threshold\n
                                   set by the admin. E.g. if the threshold is 70% and a review\n
                                   caught 71% of bugs, it is considered successful.`,
    averageAccuracyTooltipText: () => `Average accuracy is the average percentage of bugs\n
                                       that the reviewer was able to catch in their reviews\n
                                       which were analyzed`,
    dateTooltipText: () => `Selected time period to filter the reviews with submission\n
                                  date starts and ends within the time period`,
  },
});
